<template>
  <div class="scheduleContainer">
    <div class="scheduleElement" v-for="(daySchedule, day) in schedule" :key="day">
      <h2>{{ day }}</h2>
      <ul>
        <li v-for="lesson in daySchedule" :key="lesson.name">
          <a v-if="lesson.link!=null" :href="lesson.link">{{ lesson.name }}</a>
          <a v-else>{{ lesson.name }}</a>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  export default {
    props: ['schedule']
  }
</script>
