    computed: {
    weekNumber() {
      const today = new Date();
      const startOfYear = new Date(today.getFullYear(), 0, 1);
      const weekNumber = Math.ceil(((today - startOfYear) / 86400000 + startOfYear.getDay() + 1) / 7);
      return weekNumber+1;
    }
    },
    mounted() { // если парная неделя заменяю расписание
      if(this.weekNumber%2==0){
        this.scheduleAT212["Вівторок"].name=lessonById[2].lessonName;
        this.scheduleAT212["Вівторок"].link=lessonById[2].link;

        this.scheduleAT212["Середа"].name="Відпочиваємо";
        this.scheduleAT212["Середа"].link=null;

