<template>
  <div class="container">
    <div v-if="group === 'AT212'">
      <div class="scheduleElement" v-if="todaysSchedule.length">
        <h2>Розклад на сьогодні:</h2>
        <ul>
          <li v-for="lesson in todaysSchedule" :key="lesson.name">      
            <a  v-if="lesson.link!=null" :href="lesson.link">{{ lesson.name }}</a>
            <a v-else>{{ lesson.name }}</a>
          </li>
        </ul>
      </div>
      <div v-else>
        <p>Розклад на сьогодні не знайдено.</p>
      </div>
    </div>
    <div v-else>
      <!-- Add content for other groups if needed -->
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  props: ['group', 'scheduleAT212'],
  computed: {
    todaysSchedule() {
      const daysOfWeek = ["Неділя", "Понеділок", "Вівторок", "Середа", "Четвер", "П`ятниця", "Суббота"];
      const now = new Date();
      const dayOfWeek = now.getDay();
      const schedule = this.scheduleAT212[daysOfWeek[dayOfWeek]];
      return schedule ? schedule : [];
    }
  }
};
</script>